<script>
import YPage from '@/components/YPage.vue'
import Dialog from "@/components/Dialog.vue";
import {Button as TButton} from 'tdesign-vue';
import * as XLSX from 'xlsx'

const mammoth = require("mammoth/mammoth.browser");
export default {
  components: {
    Dialog,
    YPage, TButton
  },
  data() {
    return {
      current: '',//
      equationArr: [],
      equation: '',
      excelData: {},
      dormList: [],
      average: 0,
      jsonHead: { // 
        key: "",
        five: "",
        four: "",
        three: "",
        people: "",
        degree: "",
        amount: "",
        remark: ""
      },
      jsonHead2: { // 
        workBasic:"",
        star: "",
        floor: "",
        onDuty: "",
        sum: ""
      },
      wordTableData: [],
      excelTableData: [],
      wordStr: '',
      showDialogOne:false,
      showDialogTwo:false,
      tableOne:'',
      tableTwo:''
    }
  },
  mounted() {
    let data = localStorage.getItem('excelData');
    let wordModuleData = localStorage.getItem('wordStr')
    if(wordModuleData){
      this.wordStr = JSON.parse(wordModuleData);
    }
    if (data) {
      let val = JSON.parse(data);
      this.current = Object.keys(val)[0];
      this.excelData = val;
      this.getAllData();
    }
  },
  methods: {
    handleClearDormData(){
      localStorage.removeItem('excelData');
      localStorage.removeItem('wordStr')
      window.location.reload();
    },
    handleStatistics(key) {
      let arr = this.excelData[key]; //key,C3

      let obj = {
        key: key,
        total: 0,
        threeStr: '',
        fourStr: '',
        fiveStr: '',
        remark: '',
        fourTotal: '',
        fiveTotal: '',
        totalStr: '',
        threeNumber: '',
        fiveNumber: '',
        fourNumber: '',
        totalAmount: 0,
        threePercent: '',
        fourPercent: '',
        fivePercent: ''
      };
      let lowRoom = [],
          middleRoom = [];
      let three = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          },
          four = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          },
          five = {
            room: [],
            num: 0,
            high: 0,
            middle: 0,
            low: 0
          };
      let roomType = this.checkType(key);
      // console.log(arr)
      for (let item of arr) {
        let classes = item.classes.replace(/\s*/g, ""); //
        let info = classes.split(/:||/g);
        let people = info[1],
            room = info[0];
        obj.total += parseInt(people); //

        if (item.arange === '') {
          three.room.push(room);
          three.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              three.high += 1;
            } else if (people <= 6) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              three.high += 1;
            } else if (people <= 6) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              three.high += 1;
            } else if (people <= 5) {
              three.low += 1;
              lowRoom.push(room);
            } else {
              three.middle += 1;
              middleRoom.push(room);
            }
          }
        } else if (item.arange === '') {
          four.room.push(room);
          four.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              four.high += 1;
            } else if (people <= 6) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              four.high += 1;
            } else if (people <= 6) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              four.high += 1;
            } else if (people <= 5) {
              four.low += 1;
              lowRoom.push(room);
            } else {
              four.middle += 1;
              middleRoom.push(room);
            }
          }
        } else if (item.arange === '') {
          five.room.push(room);
          five.num += 1;
          if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
            if (people >= 10) {
              five.high += 1;
            } else if (people <= 6) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Gb') {
            if (people >= 8) {
              five.high += 1;
            } else if (people <= 6) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          } else if (roomType === 'Bc' || roomType === 'Gc') {
            if (people >= 7) {
              five.high += 1;
            } else if (people <= 5) {
              five.low += 1;
              lowRoom.push(room);
            } else {
              five.middle += 1;
              middleRoom.push(room);
            }
          }
        }
      }

      let totalRoom = four.num + five.num + three.num;
      let threePercent = this.getPercent(three.num, totalRoom),
          fourPercent = this.getPercent(four.num, totalRoom),
          fivePercent = this.getPercent(five.num, totalRoom);

      if (three.num !== 0) {
        obj.threeStr = '' + three.num + '' + threePercent + '%)' + three.room.join('');
        obj.threeNumber = '' + three.high + '' + three.middle + '' + three.low + ')';
      }

      obj.fourStr = '' + four.num + '' + fourPercent + '%)' + four.room.join('') + '';
      obj.fiveStr = '' + five.num + '' + fivePercent + '%)' + five.room.join('') + '';
      obj.threePercent = threePercent;
      obj.fourPercent = fourPercent;
      obj.fivePercent = fivePercent;


      let lowStr = '';
      let middleStr = '';
      if (roomType === 'Ba' || roomType === 'Bb' || roomType === 'Ga' || roomType === 'Gd') {
        lowStr = lowRoom.join('') + '750%';
        middleStr = '7-9';
      } else if (roomType === 'Gb') {
        lowStr = lowRoom.join('') + '750%';
        middleStr = '7';
      } else if (roomType === 'Bc' || roomType === 'Gc') {
        lowStr = lowRoom.join('') + '650%';
        middleStr = '6';
      }

      if (lowRoom.length > 0 && middleRoom.length > 0) {
        obj.remark = '' + middleRoom.join('') + '' + middleStr + '80%' + lowStr;
      } else if (middleRoom.length > 0) {
        obj.remark = '' + middleRoom.join('') + '' + middleStr + '80%';
      } else if (lowRoom.length > 0) {
        obj.remark = '' + lowStr;
      } else {
        obj.remark = ''
      }
      // console.log(obj.remark)
      let threeTotal = three.high * 30 + three.middle * 30 * 0.8 + three.low * 30 * 0.5;
      let fourTotal = four.high * 50 + four.middle * 50 * 0.8 + four.low * 50 * 0.5;
      let fiveTotal = five.high * 60 + five.middle * 60 * 0.8 + five.low * 60 * 0.5;

      let three100 = three.high ? three.high + '*50' : '';
      let three80 = three.middle ? '+' + three.middle + '* 50 * 80%' : '';
      let three50 = three.low ? '+' + three.low + '* 50 * 50%' : '';

      let four100 = four.high ? four.high + '*50' : '';
      let four80 = four.middle ? '+' + four.middle + '* 50 * 80%' : '';
      let four50 = four.low ? '+' + four.low + '* 50 * 50%' : '';

      let five100 = five.high ? five.high + '*60' : '';
      let five80 = five.middle ? '+' + five.middle + '* 60 * 80%' : '';
      let five50 = five.low ? '+' + five.low + '* 60 * 50%' : '';

      let totalAmount = threeTotal + fourTotal + fiveTotal;
      if (threeTotal) {
        obj.threeNumber = '' + three.high + '' + three.middle + '' + three.low + ')';
        obj.threeTotal = '' + three100 + three80 + three50 + '=' + threeTotal + ')';
      }
      if (fourTotal) {
        obj.fourNumber = '' + four.high + '' + four.middle + '' + four.low + ')';
        obj.fourTotal = '' + four100 + four80 + four50 + '=' + fourTotal + ')';
      }
      if (fiveTotal) {
        obj.fiveNumber = '' + five.high + '' + five.middle + '' + five.low + ')';
        obj.fiveTotal = '' + five100 + five80 + five50 + '=' + fiveTotal + ')';
      }

      let theTotalArr = [];
      if (threeTotal) {
        theTotalArr.push(threeTotal)
      }
      if (fourTotal) {
        theTotalArr.push(fourTotal)
      }
      if (fiveTotal) {
        theTotalArr.push(fiveTotal)
      }
      obj.totalStr = '' + theTotalArr.join('+') + '=' + totalAmount + '';

      obj.totalAmount = totalAmount;

      this.equationArr.push(totalAmount)

      // console.log(obj)
      this.dormList.push(obj);
    },
    //
    floorReward(key, threePercent, fourPercent, fivePercent, total) {
      let roomType = this.checkType(key);
      let obj = {degree: '', amount: 0, remark: ''};
      if (roomType === 'Ba') {
        if (fivePercent >= 30 && !threePercent) {
          obj.degree = '';
          obj.amount += 1900;
        } else if (fivePercent >= 20 && !threePercent) {
          obj.degree = '';
          obj.amount += 1550;
        } else if ((fourPercent + fivePercent) >= 90 && threePercent < 10) {
          obj.degree = '';
          obj.amount += 1200;
        } else if (threePercent >= 10) {
          obj.degree = '';
          obj.amount += 950;
        }
      } else if (roomType === 'Bb' || roomType === 'Bc') {
        if (fivePercent >= 30 && !threePercent) {
          obj.degree = '';
          obj.amount += 950;
        } else if (fivePercent >= 20 && !threePercent) {
          obj.degree = '';
          obj.amount += 750;
        } else if ((fourPercent + fivePercent) >= 90 && threePercent < 10) {
          obj.degree = '';
          obj.amount += 550;
        } else if (threePercent >= 10) {
          obj.degree = '';
          obj.amount += 450;
        }
      } else if (roomType === 'Gb' || roomType === 'Gd') {
        if (fivePercent >= 60 && !threePercent) {
          obj.degree = '';
          obj.amount += 1900;
        } else if (fivePercent >= 40 && !threePercent) {
          obj.degree = '';
          obj.amount += 1550;
        } else if (fivePercent >= 20 && threePercent < 10) {
          obj.degree = '';
          obj.amount += 1200;
        } else if (fivePercent < 20) {
          obj.degree = '';
          obj.amount += 950;
        }
      } else if (roomType === 'Ga' || roomType === 'Gc') {
        if (fivePercent >= 60 && !threePercent) {
          obj.degree = '';
          obj.amount += 950;
        } else if (fivePercent >= 40 && !threePercent) {
          obj.degree = '';
          obj.amount += 750;
        } else if (fivePercent >= 20 && threePercent < 10) {
          obj.degree = '';
          obj.amount += 550;
        } else if (fivePercent < 20) {
          obj.degree = '';
          obj.amount += 450;
        }
      }
      if (roomType === 'Ba' || roomType === 'Gb' || roomType === 'Gd') {
        if (total > 250) {
          obj.amount += (total - 250) * 10;
          obj.remark = `${total - 250}`
        } else if (total < 200) {
          obj.amount -= (200 - total) * 10;
          obj.remark = `${200 - total}`
        }
      } else if (roomType === 'Bc' || roomType === 'Gc' || roomType === 'Bb' || roomType === 'Ga') {
        if (total > 120) {
          obj.amount += (total - 120) * 10;
          obj.remark = `${total - 120}`
        } else if (total < 80) {
          obj.amount -= (80 - total) * 10;
          obj.remark = `${80 - total}`
        }
      }
      return obj
    },
    //
    getPercent(val, sum) {
      return Math.round(val / sum * 100)
    },
    //
    getAllData() {
      this.dormList = []; //
      for (let key in this.excelData) {
        this.handleStatistics(key);
      }
      this.equation = this.equationArr.join('+'); //
      this.createWordTable();
      this.createRewardExcelTable();
      this.handleAverage();
    },
    //excel
    changeExcel(e) {
      this.current = '';
      const files = e.target.files;
      if (files.length <= 0) {
        return false
      } else if (!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())) {
        console.log('xlsxlsx')
        return false
      }
      let selectedFile = e.target.files[0];
      // 
      const fileReader = new FileReader()
      fileReader.onload = (event) => {
        let data = event.target.result;
        let workbook = XLSX.read(data, {
          type: 'binary'
        });
        let map = new Map();
        workbook.SheetNames.forEach(function (sheetName) {
          let excelData = XLSX.utils.sheet_to_row_object_array(workbook.Sheets[sheetName]);
          // JSON
          if (excelData.length > 0) {
            map.set(sheetName, excelData)
          }
        })
        let allData = [...map]; //[...map]

        let theObj = this.getGroup(allData[0][1]);
        console.log(theObj)
        this.excelData = theObj;
        localStorage.setItem('excelData', JSON.stringify(theObj))
        // setExcelObj(theObj) //
        this.getAllData();
      }
      fileReader.onerror = function (event) {
        console.error("excel," + event.target.error.code);
      };
      // 
      fileReader.readAsBinaryString(selectedFile);
    },
    //word
    changeWord(e) {
      const files = e.target.files;
      if (files.length <= 0) {
        return false
      } else if (!/\.(doc|docx)$/.test(files[0].name.toLowerCase())) {
        console.log('docdocx')
        return false
      }
      let selectedFile = e.target.files[0];
      // 
      const fileReader = new FileReader()
      fileReader.onload = async (event) => {
        let data = event.target.result;
        const result = await mammoth.extractRawText({
          arrayBuffer: data
        });
        // console.log(result.value)
        if (!result.value) {
          console.log('word')
          return
        }
        console.log('')
        this.wordStr = result.value;
        localStorage.setItem('wordStr', JSON.stringify(result.value)) //
      }
      fileReader.onerror = function (event) {
        console.error("word," + event.target.error.code);
      };
      // 
      fileReader.readAsBinaryString(selectedFile);
    },
    //
    getGroup(data) {
      let obj = {};
      // console.log(data)
      data.forEach(item => {
        let info = item.classes.split(/|:|/g);
        let str = info[0].length >= 5 ? info[0].slice(0, 3) : info[0].slice(0, 2);
        let key = str.toUpperCase();
        if (!this.current) {
          this.current = key;
        }
        if (key && obj[key]) {
          obj[key].push(item)
        } else {
          obj[key] = obj[key] || [];
          obj[key].push(item)
        }
      });
      return obj;
    },
    //
    checkType(str) {
      str = str.toUpperCase();
      if (str === 'C3' || str === 'C4' || str === 'C5' || str === 'C6') {
        /*Ba12*/
        return 'Ba'
      } else if (str === 'D2' || str === 'D3' || str === 'D4' || str === 'D5') {
        /*Bb12D*/
        return 'Bb'
      } else if (str === 'D10' || str === 'D11' || str === 'D12') {
        /*Bc8D*/
        return 'Bc'
      } else if (str === 'D6' || str === 'D7' || str === 'D8') {
        /*Ga12D*/
        return 'Ga'
      } else if (str === 'B5' || str === 'B6' || str === 'B8') {
        /*Gb10*/
        return 'Gb'
      } else if (str === 'D9' || str === 'D13' || str === 'D14') {
        /*Gc8D*/
        return 'Gc'
      } else if (str === 'C8' || str === 'C9') {
        /*Gd12C*/
        return 'Gd'
      }
    },
    // tab
    handleTab(value) {
      this.current = value;
    },
    //1
    getGroupOrder(arr) {
      let cArr = [],
          bArr = [],
          dArr = [];
      for (let i = 0, len = arr.length; i < len; i++) {
        let keyStr = arr[i].key;
        if (keyStr[0].toUpperCase() === 'C') {
          cArr.push(arr[i])
        } else if (keyStr[0].toUpperCase() === 'B') {
          bArr.push(arr[i])
        } else if (keyStr[0].toUpperCase() === 'D') {
          dArr.push(arr[i])
        }
      }
      let cNewArr = this.getOrder(cArr);
      let bNewArr = this.getOrder(bArr);
      let dNewArr = this.getOrder(dArr);
      // console.log([...cNewArr, ...bNewArr, ...dNewArr])
      return [...cNewArr, ...bNewArr, ...dNewArr]
    },
    //2
    getOrder(arr) {
      return arr.sort((obj1, obj2) => {
        return obj1.key.slice(1, 3) - obj2.key.slice(1, 3)
      })
    },
    //
    handleAverage() {
      let str = this.equation.replace(/\s*/g, ""); //
      let arr = str.split('+');
      let total = 0;
      for (let i = 0; i < arr.length; i++) {
        total += parseInt(arr[i]);
      }
      console.log(total)
      this.average = Math.round(total / 9)
    },
    //excel
    handleExportExcel() {
      const sheet1 = this.excelTableData.map(item => {
        const obj = {}
        for (const k in item) {
          if (this.jsonHead[k]) {
            obj[this.jsonHead[k]] = item[k]
          }
        }
        return obj
      })
      const sheet2 = this.wordTableData.map(item => {
        const obj = {}
        for (const k in item) {
          if (this.jsonHead2[k]) {
            obj[this.jsonHead2[k]] = item[k]
          }
        }
        return obj
      })
      // console.log(sheet1)
      console.log(sheet2)
      // 
      const sheet1data = XLSX.utils.json_to_sheet(sheet1)
      const sheet2data = XLSX.utils.json_to_sheet(sheet2)
      // 
      const wb = XLSX.utils.book_new()
      // 
      XLSX.utils.book_append_sheet(wb, sheet1data, 'sheet1')
      XLSX.utils.book_append_sheet(wb, sheet2data, 'sheet2')
      // 
      XLSX.writeFile(wb, '.xlsx')
    },
    //1
    handleTableOne() {
      let str = this.wordStr;
      // console.log(str)
      if (!str) {
        console.log('word')
        return
      }
      this.showDialogOne = true
      this.creatTableOne(str)
    },
    //1
    creatTableOne(wordData) {
      let arr = wordData.split('[]');
      let list = this.dormList;
      let text = '';
      for (let i = 0, leni = arr.length - 1; i < leni; i++) {
        let matchArr = arr[i].match(/([a-zA-Z])(\d+)/);
        let key = matchArr[1].toUpperCase() + matchArr[2];
        for (let j = 0, lenJ = list.length; j < lenJ; j++) {
          if (key === list[j].key) {
            text += arr[i] + '<br/><h1 style="color:#333;">' + list[j].threeStr + '<br/>' + list[j].fourStr + '<br/>' + list[j].fiveStr + '</h1><br/>';
            break;
          }
        }
      }
      this.tableOne = '<div style="color:#666;">' + text + '</div>';
    },
    //2
    handleTableTwo() {
      let str = this.wordStr;
      if (!str) {
        console.log('word')
        return
      }
      this.showDialogTwo = true
      this.creatTableTwo(str)
    },
    //2
    creatTableTwo(wordData) {
      let arr = wordData.split('[]');
      let list = this.dormList;
      console.log(arr)
      // console.log(list)
      let text = '';
      let totalStr = '';
      let totalAmount = 0;
      for (let i = 0, leni = arr.length - 1; i < leni; i++) {
        let matchArr = arr[i].match(/([a-zA-Z])(\d+)/);
        let key = matchArr[1].toUpperCase() + matchArr[2];
        for (let j = 0, lenJ = list.length; j < lenJ; j++) {
          if (key === list[j].key) {
            text += arr[i] + '<br/><h1 style="color:#333;">' + list[j].remark + '<br/>' + list[j].fourTotal +
                '<br/>' + list[j].fiveTotal + '<br/>' + list[j].totalStr + '</h1><br/>';
            totalAmount += list[j].totalAmount;
            if (i === (leni - 1)) {
              totalStr += list[j].totalAmount + '=' + Math.round(totalAmount / leni) + '<br/>' +
                  totalAmount + '';
            } else {
              totalStr += list[j].totalAmount + '+';
            }
            break;
          }
        }
      }
      console.log(totalStr, totalAmount)
      this.tableTwo = '<div style="color:#666;">' + text + '<br/><b style="color:#333;">' + totalStr + '<b/></div>';
    },
    //word
    createWordTable(){
      let val = JSON.parse(JSON.stringify(this.dormList)); //
      let newDormList = this.getGroupOrder(val);
      let tableMap = new Map();
      newDormList.forEach(item => {
        let { amount } = this.floorReward(item.key, item.threePercent, item.fourPercent, item.fivePercent, item.total)
        let obj = {
          order:1,
          name:'',
          dormStar1:0,
          dormStar2:0,
          dormStar3:0,
          dormFloor1:0,
          dormFloor2:0,
          dormFloor3:0,
          basic:1900,
          fullWork:300,
          workBasic:'1900 + 300',
          star:'',
          floor:'',
          onDuty:0,
          sum:0
        }
        if(item.key === 'C3' || item.key === 'C4'){
          obj.name = "(C3C4)";
          obj.order = 1;
          if(tableMap.has("C3C4")){
            let val = tableMap.get("C3C4");
            val.dormStar2 = item.totalAmount;
            val.dormFloor2 = amount;
            val.star = val.dormStar1 + '+' + val.dormStar2;
            val.floor = val.dormFloor1 + '+' + val.dormFloor2;
            val.sum = val.dormStar1 + val.dormStar2 + val.dormStar3 + val.dormFloor1 + val.dormFloor2 + val.dormFloor3 + val.basic + val.fullWork + val.onDuty;
          }else{
            obj.dormStar1 = item.totalAmount;
            obj.dormFloor1 = amount;
            obj.workBasic = obj.basic + '+' + obj.fullWork;
            tableMap.set("C3C4",obj)
          }
        }else if(item.key === 'C8' || item.key === 'C9'){
