            obj.dormFloor1 = amount;
            obj.workBasic = obj.basic + '+' + obj.fullWork;
            tableMap.set("D2D3",obj)
          }
        }else if(item.key === 'D4' || item.key === 'D5'){
          obj.name = "(D4D5)";
