          obj.floor = amount;
          obj.workBasic = obj.basic + '+' + obj.fullWork;
          obj.sum = obj.dormStar1 + obj.dormStar2 + obj.dormStar3 + obj.dormFloor1 + obj.dormFloor2 + obj.dormFloor3 + obj.basic + obj.fullWork + obj.onDuty;
          tableMap.set("C6",obj);
        }
      })
      // console.log(tableMap)
      let arr = Array.from(tableMap.values());
      this.wordTableData = this.getTableOrder(arr);
